@mixin folow-btn($w, $h){
	cursor: pointer;
	text-transform: uppercase;
	font-size: 0.75em;
	font-family: "Roboto";
	font-weight: 700;
	text-align: center;
	display: inline-block;
    line-height: $h;
	float: right;
}
@mixin tableData($px){ 
	padding: 0.75rem 1.25rem;
	line-height: 1.5rem;
	vertical-align: top;
	border-top: $px solid #ddd;
}
@mixin getMove(){
	float: right;
	margin-top: 0.4375rem;
	.pull-left{
		float: left;
		@include breakpoint('medium-small') {
			height: 1.5rem;
			width: 1.5rem;
			display: block;
		}
	}
	span{
		height: 0.125rem;
		width: 1.3125rem;
		background: #d1d3d5;
		display: block;
		margin-bottom: 3px;
	}
	.move{
		float: left;
		margin-right: 0.625rem;
		position: relative;
		top: -4px;
		text-transform: uppercase;
		font-size: 0.75em;
		display: none;
		@include breakpoint('small') {
			z-index: -1;
		}
	}
}
@mixin unfollowBtnLbl(){
	@include folow-btn(7.5rem, 2rem);
	margin: 0.5rem 0 0 1.25rem;
	&.mtp{
		margin-top: -6px;
		margin-bottom: 25px;
	}
}
@mixin followBtnLbl(){
	@include folow-btn(7.5rem, 2rem);
	padding: 0 1.5625rem;
	margin-top: 0.5rem;
	&.mtp{
		margin-top: -6px;
		margin-bottom: 25px;
	}
}

.myview-settings {
	padding-bottom: 50px !important;
	margin-bottom: 3.125rem;
	&.myview-settings-registration {
		.publicationPan h2 {
			width: auto;
		}
		.saveoption {
			width: 100%;
		}
	} 
	h1{
		margin-bottom: 0rem;
		float: left;
		@include breakpoint('medium-small') {
			font-size: 2rem;
		}
	}
	.mysettingsHead{
		border-bottom: 1px solid #d1d3d4;
		padding-bottom: 15px;
		margin-bottom: 1.25rem;
		padding-top: 50px;
		@include breakpoint('small') {
			margin-bottom: 0rem;
			border-bottom: none;
			padding-top: 0;
		}
	}
	.mtop-10{
		margin-top: 0.625rem;
	}
	.mb-10{
		margin: 0.625rem 0 1.25rem;
	}
	.desktophide{
		display: none !important;
	}
	.sub-heading-text{
		font-size: 1.125em;
		margin-bottom: 50px;
		p{
			line-height: 1rem;
			font-style: normal;
			font-family: "PT Serif Caption";
			font-weight: 400;
			font-size: 1.125em;
			@include breakpoint('medium-small') {
				font-family: "Roboto";
				font-size: 0.85em;
				margin: 0rem;
				padding: 0rem;
				line-height: 1.375rem;
			}
		}
	}
	.publicationPan{
		margin-bottom: 3.125rem;
		border: 1px solid #d9dadb;
		h2{
			text-align: left;
			float: left;
			width: 29rem;
			margin-top: -5px;
			line-height: 2.5rem;
			@include breakpoint('medium-small') {
				font-size: 1.25em;
				margin-bottom: 0.75rem;
				margin-top: 0px;
				float: none;
				width: auto;
				line-height: 1.625rem;
			}
		}
		.subscribed{
			border-left: 1px solid #d9dadb;
			float: left;
			cursor: pointer;
			padding-left: 1.25rem;
			margin: 3px 20px;
			vertical-align: top;
			line-height: 30px;
			position: relative;
			@include breakpoint('medium-small') {
				font-size: 0.875em;
				border-left: none;				
				padding-left: 0rem;
				margin: 0rem;
			}
			a{
				color: #fff;
				&:hover{
					text-decoration: none;
				}
			} 
		}
		.subscribe{
			float: left;
			width: 7.5rem;
			height: 2rem; 
			margin: -4px 0 0 1.25rem;
			line-height: 2rem;
			font-size: 0.75rem;
			cursor: pointer;
			padding-left: 0rem;
			font-family: "Roboto";
			@include breakpoint('small') {
				font-size: 0.875em;
				border-left: none;				
				padding-left: 0rem;
				margin: 0 !important;
			}
		}
	}
	.table-responsive {
		width: 100%;
		overflow-y: hidden;
		-ms-overflow-style: -ms-autohiding-scrollbar;
		.table{
			border-collapse: collapse;
			width: 100%;
			max-width: 100%;
			font-family: "Roboto Condensed";
			font-style: normal;
			@include breakpoint('small') {
				display: block;
				position: relative;
			}
			tbody{
				 tr{
					-webkit-transition: all 0.2s ease;
					-moz-transition: all 0.2s ease;
					-o-transition: all 0.2s ease;
					-ms-transition: all 0.2s ease;
					transition: all 0.2s ease; 
					td{
						@include tableData(1px);
						vertical-align: middle;
					}
					.followingBtn{
						@include folow-btn(7.5rem, 2rem);
						padding: 0 1.625rem;
						&:hover{
							@include breakpoint('large') {
								background: inherit;
								color: inherit;
							}
							@include breakpoint('small') {
								background: url(/dist/img/sprite-image.png) no-repeat -3px -235px;
							}
						}
						@include breakpoint('medium-small'){
							background: url(/dist/img/sprite-image.png) no-repeat -3px -235px;
							height: 1.875rem;
							width: 1.875rem;
							border: none;
							padding: 0 1rem;
							display: inline-block;
							margin-left: 0.625rem;
							text-indent: -9999px;
						}
					}
					.followBtn{
						@include folow-btn(7.5rem, 2rem);
						padding: 0 2.25rem;
						@include breakpoint('medium-small'){
							background: url(/dist/img/sprite-image.png) no-repeat -3px -266px;
							height: 1.875rem;
							width: 1.875rem;
							display: inline-block;
							margin-left: 0.625rem;
							border: none;
							padding: 0 1rem;
							text-indent: -9999px;
						}
					}
					&.followingrow{
						.rowlines{
							@include getMove();
						}
						&:hover{
							background: #f5f5f5;
							@include breakpoint('large') {
								background: #fff;
							}
							cursor: move;
							& .rowlines{
								span{
									background: #acacad;
									@include breakpoint('large'){
										background: #d1d3d5;
									}
								}
							}
							& .move{
								@include breakpoint('large') {
									display: none;
								}
								display: block;
							}
						}
					}
					&.followrow{
						background: #f9f9f9;
						@include breakpoint('small') {
							background: $color-white;
						}
						.rowlines{
							@include getMove();
						}
						&.frow{
							border-top: 2px solid #d1d3d4;
							@include breakpoint('small') {
								border-top: none;
							}
						}
						& .move{
							display: none;
						}
					}
				 }
			}
			thead{
				tr{
					@include breakpoint('small') {
						&.expandHide{
							display: none;
							&.collapseshow{
								display: block !important;
							}
						}
					}
				}
				th{
					@include tableData(0px);
					background: #f9f9f9;
					@include breakpoint('small') {
						padding: 0.5rem 0rem 0.625rem 0.625rem;
					}
					p{
						font-size: 1em; 
						text-align: left;
						padding: 0.75rem 0 0;
						font-weight: normal;
						@include breakpoint('medium-small') {
							padding: 0;
							font-size: 1em;
							font-weight: normal;
						}
						
						&.nosubscr{
							margin: -40px 0 0;
							font-weight: normal;
						} 
					} 
					&.firstrow{
						padding: 2.5rem 1.25rem 0;
						@include breakpoint('medium-small') {
							padding: 1.25rem 0.625rem 0.625rem;
						}
					}
					&.nopad{
						padding: 0rem 1.25rem;
						@include breakpoint('small') {
							padding:  0rem 0.625rem;
						}
					}
					&.pbot{
						padding: 0rem 1rem 1.25rem 27px;
					}
					& .rowlines{
						@include getMove();
						margin-top: -1.5rem;
						.mv{						
							background: url(/dist/img/drag-icon.jpg) no-repeat 0 0;
							height: 50px;
							width: 21px;
							display: block;
							margin-top: -15px;
							
							@include breakpoint('medium-small') {
								background: url(/dist/img/drag-icon2.jpg) no-repeat 0 0;
								height: 40px;
								width: 40px;
								position: relative;
								left: -15px;
								display: block;
							}
						}
						@include breakpoint('medium-small') {
							top: 4px;
							margin-top: 0px;
						}
						@include breakpoint('large') {
							height: 30px;
							width: 30px;
						}
						& .vline{
							border: 1px solid #dadada;
							height: 40px;
							width: 1px;
							float: left;
							position: relative;
							top: -13px;
							right: 22px;
							@include breakpoint('medium-small'){
								display: none;
							}
							@include breakpoint('medium'){
								right: 12px;
							}
						}
					}
					& .accordionImg{
						span{
							background: url(/dist/img/sprite-image.png) no-repeat 0 -21px;
							height: 1.0625rem;
							width: 2rem;
							cursor: pointer;
							display: inline-block;
							margin-right: 10%;
							@include breakpoint('medium-small') {
								/*background: url(/dist/img/sprite-image.png) no-repeat 0 -81px;
								height: 0.75rem;
								width: 1.4375rem;*/
								position: relative;
								left: -10px;
								&.expanded{
									position: relative;
									left: 0;
									/*background-position: 0 -48px !important;*/
									background-position: 0 0 !important;
								}
							}
							@media screen and (min-width: 667px) and (max-width: 767px){
								margin-right: 3.125rem;
							}
							&.expanded{
								background-position: 0 0;
							}
							@media screen and (min-width: 801px) and (max-width: 1130px){
								margin-right: 50%;
							}
						}
					}
					&.notsubscr{
						text-align: left;
						padding-bottom: 1.5rem;
						padding-top: 0rem;
					}
				} 
				.followAllBtn{
					@include followBtnLbl();
					min-width: 130px;
					@include breakpoint('medium-small') {
						min-width: initial;
						min-width: auto;
					}
					@include breakpoint('small') {
						min-width: initial;
						min-width: auto;
					}
				}
				.followinglbl{
					@include followBtnLbl();
					cursor: text;
					@include breakpoint('medium-small') {
						margin: 0px !important;
						float: left;
					}
				}
				.unfollowAllBtn{
					@include unfollowBtnLbl();
					padding: 0 1rem;
					margin-left: 0.5rem;
					@include breakpoint('medium-small') {
						min-width: initial;
						min-width: auto;
					}
					@include breakpoint('small') {
						min-width: initial;
						min-width: auto;
					}
					@media screen and (min-width: 667px) and (max-width: 767px){
						padding: 0 1.25rem;
					}
				}
				.followlbl{
					@include unfollowBtnLbl();
					padding: 0;
					cursor: text;
					min-width: 130px;
					@include breakpoint('medium-small') {
						min-width: initial;
						min-width: auto;
					}
					@include breakpoint('small') {
						min-width: initial;
						min-width: auto;
					}
					@include breakpoint('medium-small') {
						margin: 0px !important;
						float: left;
					}
				}
			}
			.wd-55{
				width: 55%;
				@include breakpoint('small') {
					width: 65%;
				}
				@include breakpoint('medium-small') {
					width: 45%;
				}
			}
			.wd-25{
				width: 25%;
				&.pbot{
					padding-left: 0rem;
				}
				@include breakpoint('small') {
					width: 20%;
					padding: 0.5rem 0rem 0rem 0.625rem;
					text-align: right;
				}
				@include breakpoint('medium-small') {
					width: 25%;
				}
			}
			.wd-15{
				width: 15%;
				@include breakpoint('small') {
					width: 10%;
				}
				@include breakpoint('medium-small') {
					width: 15%;
				}
			}
			.graybg{
				background: #f9fafa;
			}
		}
  }
