		.desktopMode{
			margin-left: -1.875rem;
		}
		.firstrow{
			h2{
				width: auto;
			}
		}
	}
}
@media screen and (min-width: 768px) and (max-width: 1024px){
	.myview-settings{
		.firstrow h2{
			width: 13rem;
		}
	}
}
@media screen and (min-width: 800px) and (max-width: 900px){
	.myview-settings{
		.desktopMode{
			margin-left: -2.5rem;
		}
	}
}
@media screen and (min-width: 640px) and (max-width: 900px){
	.myview-settings{
		.desktopMode{
			margin-left: -3.4375rem;
		}
	}
}

@media screen and (min-width: 480px) and (max-width: 640px){
	.myview-settings{
		.accordionStatus{
			&.hideRow{
				display: none;
			}
		}
	}
}
@include breakpoint('small') {
	.myview-settings{ 
		.smfollowingBtn{
			margin-top: -0.5rem;
			
			.unfollowAllBtn{
				background-position: -3px -236px;
				text-indent: -8.75rem;
				color: $color-abbey-gray !important;
				border: none !important;
				background-color: inherit !important;
				clear: both;
			} 
			.followAllBtn{
				background-position: -3px -266px;
				text-indent: -124px;
				border: none !important;
				padding: 0 0 !important;
				float: right;
				clear: both;
			}
		}
		.accordionStatus{
			&.hideRow{
				display: none;
			}
		}
	}
}
@include breakpoint('medium-small') {
	.hidden-xs{
		display: none !important;
	}
	.smfollowingBtn{
		margin: 0.1rem 0;
		span{
			border: none;
			height: 30px !important;
			width: 30px !important;
			line-height: 1.75rem !important;
			&.unfollowAllBtn{
				background: url(/dist/img/sprite-image.png) no-repeat -3px -236px;
				text-indent: -8.75rem;
				color: $color-abbey-gray !important;
				border: none !important;
				background-color: inherit !important; 
			} 
			&.followAllBtn{
				background: url(/dist/img/sprite-image.png) no-repeat -3px -266px;
				text-indent: -124px;
				border: none !important;
				background-color: inherit !important;
				padding: 0 0 !important;
			}
		}		
	} 
}
 
/************ drag and drop styles ***********/
.drop {
	-webkit-animation: drop 1s ease;
	-moz-animation: drop 1s ease;
	-ms-animation: drop 1s ease;
	-o-animation: drop 1s ease;
	animation: drop 1s ease;
}

.followingrow.over{
	background: #1191d1;
}
 
@-moz-keyframes drop {
	0%, 100% {
		background: #1191d1;
		color: $color-white;
	}
	50% {
		color: #1191d1;
		background: $color-white;
	}
}
@-webkit-keyframes "drop" {
	0%, 100% {
		background: #1191d1;
		color: $color-white;
	}
	50% {
		color: #1191d1;
		background: $color-white;
	}
}
@-ms-keyframes "drop" {
	0%, 100% {
		background: #1191d1;
		color: $color-white;
	}
	50% {
		color: #1191d1;
		background: $color-white;
	}
}
@-o-keyframes "drop" {
	0%, 100% {
		background: #1191d1;
		color: $color-white;
	}
	50% {
		color: #1191d1;
		background: $color-white;
	}
